<template>
  <div class="addtHtml">
  	<div class="addList">
  		<h3>
  			<span style="color:red;">*</span>
  			<span class="inputLeft">标题:</span>
  			<input v-model="title" type="" placeholder="请输入">
  		</h3>
  	</div>
  	<div class="addList">
  		<h3>
  			<span style="color:red;">*</span>
  			<span class="inputLeft">摘要:</span>
  		</h3>
  		<textarea v-model="excerpt" id="ksjkds" type="" placeholder="请输入"></textarea>
  	</div>
  	<div class="addList">
  		<h3 class="imgH3" data-id="1">
  			<span style="color:red;">*</span>
  			<span class="inputLeft">封面:</span>
  		</h3>
  		<van-uploader :after-read="onRead">
  			<div class="butIcons">选择封面</div>
		</van-uploader>
  	</div>
  	<div class="addList">
  		<h3>
  			<span style="color:red;">*</span>
  			<span class="inputLeft">正文:</span>
  		</h3>
  		<vue-html5-editor :content="content" :height="180"></vue-html5-editor>
  	</div>
  	<br />
  	<div class="butAdd but-80-red" v-on:click="add">提交</div>
  	
  </div>
</template>

<script>
$("title").text("上传图片");
export default {
  name: 'addtHtml',
  props: ['value'],
  data () {
    return {
    		content: '',
    		title:'',
    		excerpt: '',
    		show: false,
    		textnames: new Date().getTime(),//这里防止多个富文本发生冲突
        editor:'',//保存simditor对象
        toolbar: ['bold', 'italic', 'underline', 'strikethrough',
            'color', '|', 'ol', 'ul', 'blockquote', 'code', '|',
            'link', 'image', '|', 'indent', 'outdent'
        ]//自定义工具栏
    }
  },
  created:function(){
    
  },
  methods: {
    
  	onRead(file) {
      console.log(file)
      if(!file.file.name){return false;}
      var _this = this;
      $("#msgLoding").show();
		var formData = new FormData();
		formData.append("file", $('.van-uploader__input')[0].files[0]);
    		_this.utilAjaxFile('http://121.14.27.27:88/wp-json/wp/v2/media',formData,function(res){
    			$("#msgLoding").hide();
    			$(".butIcons").hide();
    			
    			$(".imgH3").attr("data-id",res.id);
    			$(".van-uploader:after").css("content","")
    			var img = $(".van-uploader img").attr("src");
		    if(img){
		      	$(".van-uploader img").attr("src",file.content);
		    }else{
		      	var imgHtml = '<img class="img" src="'+file.content+'" style="width:24rem;height:16rem; display: block;margin:10px auto;">'
		      	$(".van-uploader").append(imgHtml)
		    }
		},"post");
		
		$(".imgH3").click(function(){
			
		})
    },
    add: function (event) {
    	
    		var _this = this;
    		if(localStorage.user){
    			var user = JSON.parse(localStorage.user);
    		}else{
    			_this.utilMsg("请先登录");
    			return false;
    		}
    		var author = user.id;
    		var content = $(".content").html();
    		var title = this.title;
    		var featured_media = $(".imgH3").attr("data-id");
    		if(!title){
    			_this.utilMsg("请填写标题");
    			return false;
    		}else if(!content){
    			_this.utilMsg("请填写内容");
    			return false;
    		}
    		$("#msgLoding").show();
    		var datas = {"content":content,"title":title,"author":author,"excerpt":this.excerpt,"status":'publish',"featured_media":featured_media}
    		_this.utilAjax('http://121.14.27.27:88/wp-json/wp/v2/posts',datas,function(res){
    			$("#msgLoding").hide();
			 _this.utilMsg("发布成功")
			 $(".van-uploader img").remove();
			 $(".butIcons").show();
			 $(".imgH3").attr("data-id","");
			 $(".vue-html5-editor .content").html("");
			 _this.title = '';
			 _this.content = '';
			 _this.excerpt = '';
			 if(_this.utilIsLocalhost()==false){
				var content = {"type":"add_post_end"}
			    	var ttfData = JSON.stringify(content);
			    ttf.nslog(ttfData)
		  	 }
		},"post");
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #999;z-index: 1;border: dashed 1px #c7c7c7;
	}
</style>



