<template>
  <div class="indexHtml">
	   <br/>
	   <router-link to="/information">资讯列表</router-link>
	   <br/>
	   <router-link to="/information/37">资讯详情</router-link>
	   <br />
	   <router-link to="/post/add">资讯添加</router-link>
	   <br />
	   <router-link to="/user/user">个人中心</router-link>
	   <br />
	   <router-link to="/user/collection">我的收藏</router-link>
	   <br />
	   <router-link to="/index/transfer/transferred">转让</router-link>
	   <br />
	   <router-link to="/index/transfer/transferredDetail">转让店铺填写</router-link>
	   <br />
	   <router-link to="/index/transfer/shopInfo">店铺信息</router-link>
	   <br />
	   <router-link to="/index/transfer/order">订单信息</router-link>
	   <br />
	   <router-link to="/user/certification">实名认证</router-link>
	   <br />
	   
	   <app-footer></app-footer>
  </div>
</template>

<script>
$("title").text("列表");
export default {
  name: 'indexHtml',
  data () {
    return {
    	listDatas:'',
    	msg: '',
    	input:'',
    	message:''
    }
  },
  created:function(){
  	var _self=this;
  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts',{},function(data){
		_self.listDatas = eval(data);
	})
	if(this.utilIsLocalhost()==true){
		_self.login();
	}
  	
  }
  ,
  methods: {
    change: function (event) {
    		this.msg = $("p").text()
    },
    //分享
    share: function (type) {
    		var content = {"type":type,"title":"title","info":"info","img":"img","id":"id"}
        	var data = JSON.stringify(content);
        	console.log(data)
		ttf.nslog(data)
    },
    //注册
    register: function (type) {
    		var _self=this;
		var username = '15259';
		var password = '123456';
		var roles = 'author';//对应值为subscriber订阅者，contributor投稿者，author作者
		var data = {"username":username,"password":password,"roles":roles}
		$.post('http://121.14.27.27:88/wp-json/wp/v2/users/',data,function(data){
			console.log(data)
		})
    },
    //登陆
    login: function (type) {
    		var _self=this;
		var username = '15259';
		var password = '123456';
		var data = {"username":username,"password":password}
		$.post('http://121.14.27.27:88/wp-json/jwt-auth/v1/token',data,function(res){
			console.log(res)
			localStorage.setItem("user",JSON.stringify(res))
		})
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	a{display: block;text-align:center;font-size: 2rem;color: #42B983;}
</style>



