// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueRouter from 'vue-router'
import $ from 'jquery'
import VueHtml5Editor from 'vue-html5-editor'
import VueScroller from 'vue-scroller'
import Util from '@/util/util'
import {ttf} from '@/util/ttf'
import Vant from 'vant';
import 'vant/lib/vant-css/index.css';
import VueLazyload from 'vue-lazyload'

Vue.use(VueHtml5Editor, {
    // 全局组件名称，使用new VueHtml5Editor(options)时该选项无效 
    name: "vue-html5-editor",
    // 是否显示模块名称，开启的话会在工具栏的图标后台直接显示名称
    showModuleName: false,
    // 配置图片模块
    image: {
        // 文件最大体积，单位字节  max file size
        sizeLimit: 512 * 1024,
        // 上传参数,默认把图片转为base64而不上传
        upload: {
            //url: 'http://121.14.27.27:88/wp-json/wp/v2/media',
			url: null,
            headers: {
//          		'Authorization':'Bearer '+JSON.parse(localStorage.user).token
            },
            params: {},
            fieldName: 'file'
        },
        // 压缩参数,默认使用localResizeIMG进行压缩,设置为null禁止压缩
        compress: {
            width: 320,
            height: 200,
            quality: 80
        },
        // 响应数据处理,最终返回图片链接
        uploadHandler(responseText){
            //default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
            var json = JSON.parse(responseText)
            if (!json.ok) {
                alert(json.msg)
            } else {
                return json.data
            }
        }
    },
    // 语言，内建的有英文（en-us）和中文（zh-cn）
    language: "zh-cn",
    // 隐藏不想要显示出来的模块
    hiddenModules: [],
    // 自定义要显示的模块，并控制顺序
    // keep only the modules you want and customize the order.
    // can be used with hiddenModules together
    visibleModules: [
        "text",
        "color",
        "font",
        "align",
        "list",
        "link",
        "unlink",
        "tabulation",
        "image",
        "hr",
        "eraser",
        "undo",
        "full-screen",
    ],
    // 扩展模块，具体可以参考examples或查看源码
    modules: {
        //omit,reference to source code of build-in modules
    }
})
Vue.use(VueRouter)
Vue.use(VueScroller)
Vue.use(Util)
Vue.use(Vant);
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: require('./assets/img.png'),
  loading: require('./assets/img.png'),
  attempt: 1
})
/*头部*/
Vue.component('app-header',() => import('./components/header'))
/*导航*/
Vue.component('app-nav', () => import('./components/nav'))
/*底部*/
Vue.component('app-footer', function (resolve) {
  require(['./components/footer'], resolve)
})

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {
  	App
  }
})



